.App-header {
  /* background-color: #262736; */
  width: inherit;
  background-color: rgba(0, 0, 0, 0.3);
  /* background-color: white; */
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  -webkit-animation: enter ease-in-out 2s forwards;
